.iziModal {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  background: #FFF;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
  transition: margin-top 0.3s ease, height 0.3s ease;
  transform: translateZ(0);
  * {
    -webkit-font-smoothing: antialiased;
  }
  &::after {
    content: '';
    width: 100%;
    height: 0px;
    opacity: 0;
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: 1;
    background: -moz-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.35) 100%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(0, 0, 0, 0)), color-stop(100%, rgba(0, 0, 0, 0.35)));
    background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.35) 100%);
    background: -o-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.35) 100%);
    background: -ms-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.35) 100%);
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.35) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#59000000',GradientType=0 );
    transition: height 0.3s ease-in-out, opacity 0.3s ease-in-out;
    pointer-events: none;
  }
  &.hasShadow::after {
    height: 30px;
    opacity: 1;
  }
  .iziModal-progressbar {
    position: absolute;
    left: 0;
    top: 0px;
    width: 100%;
    z-index: 1;
    > div {
      height: 2px;
      width: 100%;
    }
  }
  .iziModal-header {
    background: #88A0B9;
    padding: 14px 18px 15px 18px;
    box-shadow: inset 0 -10px 15px -12px rgba(0, 0, 0, 0.3), 0 0 0px #555;
    overflow: hidden;
    position: relative;
    z-index: 10;
  }
  .iziModal-header-icon {
    font-size: 40px;
    color: rgba(255, 255, 255, 0.5);
    padding: 0 15px 0 0;
    margin: 0;
    float: left;
  }
  .iziModal-header-title {
    color: #FFF;
    font-size: 18px;
    font-weight: 600;
    line-height: 1.3;
  }
  .iziModal-header-subtitle {
    color: rgba(255, 255, 255, 0.6);
    font-size: 12px;
    line-height: 1.45;
  }
  .iziModal-header-title, .iziModal-header-subtitle {
    display: block;
    margin: 0;
    padding: 0;
    font-family: 'Lato', Arial;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: left;
  }
  .iziModal-header-buttons {
    position: absolute;
    top: 50%;
    right: 10px;
    margin: -17px 0 0 0;
  }
  .iziModal-button {
    display: block;
    float: right;
    z-index: 2;
    outline: none;
    height: 34px;
    width: 34px;
    border: 0;
    padding: 0;
    margin: 0;
    opacity: 0.3;
    border-radius: 50%;
    transition: transform 0.5s cubic-bezier(0.16, 0.81, 0.32, 1), opacity 0.5s ease;
    background-size: 67% !important;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-tap-highlight-color: transparent;
    /* For some Androids */
  }
  .iziModal-button-close {
    background: url('data:image/png;base64,') no-repeat 50% 50%;
  }
  .iziModal-button-fullscreen {
    background: url('data:image/png;base64,') no-repeat 50% 50%;
  }
  &.isFullscreen .iziModal-button-fullscreen {
    background: url('data:image/png;base64,') no-repeat 50% 50%;
  }
  .iziModal-button-close:hover {
    transform: rotate(180deg);
  }
  .iziModal-button:hover {
    opacity: 0.8;
  }
  .iziModal-header.iziModal-noSubtitle {
    height: auto;
    padding: 10px 15px 12px 15px;
    .iziModal-header-icon {
      font-size: 23px;
      padding-right: 13px;
    }
    .iziModal-header-title {
      font-size: 15px;
      margin: 3px 0 0 0;
      font-weight: 400;
    }
    .iziModal-header-buttons {
      right: 6px;
      margin: -16px 0 0 0;
    }
    .iziModal-button {
      height: 30px;
      width: 30px;
    }
  }
}

/* RTL */

.iziModal-rtl {
  direction: rtl;
  .iziModal-header {
    padding: 14px 18px 15px 40px;
  }
  .iziModal-header-icon {
    float: right;
    padding: 0 0 0 15px;
  }
  .iziModal-header-buttons {
    right: initial;
    left: 10px;
  }
  .iziModal-button {
    float: left;
  }
  .iziModal-header-title, .iziModal-header-subtitle {
    text-align: right;
    font-family: Tahoma, 'Lato', Arial;
    font-weight: 500;
  }
  .iziModal-header.iziModal-noSubtitle {
    padding: 10px 15px 12px 40px;
    .iziModal-header-icon {
      padding: 0 0 0 13px;
    }
  }
}

/* LIGHT THEME */

.iziModal {
  &.iziModal-light {
    .iziModal-header-icon {
      color: rgba(0, 0, 0, 0.5);
    }
    .iziModal-header-title {
      color: #000;
    }
    .iziModal-header-subtitle {
      color: rgba(0, 0, 0, 0.6);
    }
    .iziModal-button-close {
      background: url('data:image/png;base64,') no-repeat 50% 50%;
    }
    .iziModal-button-fullscreen {
      background: url('data:image/png;base64,') no-repeat 50% 50%;
    }
    &.isFullscreen .iziModal-button-fullscreen {
      background: url('data:image/png;base64,') no-repeat 50% 50%;
    }
  }
  .iziModal-loader {
    background: #FFF url(data:image/svg+xml;base64,) no-repeat 50% 50%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 9;
  }
  .iziModal-content-loader {
    background: url(data:image/svg+xml;base64,) no-repeat 50% 50%;
  }
  .iziModal-content {
    &:before {
      content: '';
      display: table;
    }
    &:after {
      content: '';
      display: table;
      clear: both;
    }
    zoom: 1;
    width: 100%;
    -webkit-overflow-scrolling: touch;
    /*overflow-y: scroll;*/
  }
  .iziModal-wrap {
    width: 100%;
    position: relative;
    -webkit-overflow-scrolling: touch;
    overflow-scrolling: touch;
  }
  .iziModal-iframe {
    border: 0;
    margin: 0 0 -6px 0;
    width: 100%;
    transition: height 0.3s ease;
  }
}

.iziModal-overlay {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.iziModal-navigate {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  pointer-events: none;
}

.iziModal-navigate-caption {
  position: absolute;
  left: 10px;
  top: 10px;
  color: white;
  line-height: 16px;
  font-size: 9px;
  font-family: 'Lato', Arial;
  letter-spacing: 0.1em;
  text-indent: 0;
  text-align: center;
  width: 70px;
  padding: 5px 0;
  text-transform: uppercase;
  display: none;
  &::before, &::after {
    position: absolute;
    top: 2px;
    width: 20px;
    height: 20px;
    text-align: center;
    line-height: 14px;
    font-size: 12px;
    content: '';
    background-size: 100% !important;
  }
  &:before {
    left: 0;
    background: url('data:image/png;base64,') no-repeat 50% 50%;
  }
  &:after {
    right: 0;
    background: url('data:image/png;base64,') no-repeat 50% 50%;
  }
}

.iziModal-navigate > button {
  position: fixed;
  bottom: 0;
  top: 0;
  border: 0;
  height: 100%;
  width: 84px;
  background-size: 100% !important;
  cursor: pointer;
  padding: 0;
  opacity: 0.2;
  transition: opacity 0.3s ease;
  pointer-events: all;
  margin: 0;
  outline: none;
  &:hover {
    opacity: 1;
  }
}

.iziModal-navigate-prev {
  left: 50%;
  background: url('data:image/png;base64,') no-repeat 50% 50%;
}

.iziModal-navigate-next {
  right: 50%;
  background: url('data:image/png;base64,') no-repeat 50% 50%;
}

.iziModal {
  &.isAttachedTop {
    .iziModal-header {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
    margin-top: 0 !important;
    margin-bottom: auto !important;
    border-top-left-radius: 0 !important;
    border-top-right-radius: 0 !important;
  }
  &.isAttachedBottom {
    margin-top: auto !important;
    margin-bottom: 0 !important;
    border-bottom-left-radius: 0 !important;
    border-bottom-right-radius: 0 !important;
  }
  &.isFullscreen {
    max-width: 100% !important;
    margin: 0 !important;
    height: 100% !important;
    border-radius: 0 !important;
  }
  &.isAttached {
    border-radius: 0 !important;
  }
  &.hasScroll .iziModal-wrap {
    overflow-y: auto;
    overflow-x: hidden;
  }
}

html {
  &.iziModal-isOverflow {
    overflow: hidden;
    body {
      overflow-y: scroll;
      position: relative;
    }
  }
  &.iziModal-isAttached {
    body {
      overflow-y: scroll;
      position: relative;
    }
    overflow: hidden;
  }
}

/* SCROLL */

.iziModal {
  ::-webkit-scrollbar {
    overflow: visible;
    height: 7px;
    width: 7px;
  }
  ::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.2);
    background-clip: padding-box;
    border: solid transparent;
    border-width: 0px;
    min-height: 28px;
    padding: 100px 0 0;
    box-shadow: inset 1px 1px 0 rgba(0, 0, 0, 0.1), inset 0 -1px 0 rgba(0, 0, 0, 0.07);
    &:active {
      background-color: rgba(0, 0, 0, 0.4);
    }
  }
  ::-webkit-scrollbar-button {
    height: 0;
    width: 0;
  }
  ::-webkit-scrollbar-track {
    background-clip: padding-box;
    border: solid transparent;
    border-width: 0 0 0 2px;
  }
  &.transitionIn {
    .iziModal-header {
      -webkit-animation: iziM-slideDown 0.7s cubic-bezier(0.7, 0, 0.3, 1);
      -moz-animation: iziM-slideDown 0.7s cubic-bezier(0.7, 0, 0.3, 1);
      animation: iziM-slideDown 0.7s cubic-bezier(0.7, 0, 0.3, 1);
      .iziModal-header-icon {
        -webkit-animation: iziM-revealIn 1s cubic-bezier(0.16, 0.81, 0.32, 1) both;
        -moz-animation: iziM-revealIn 1s cubic-bezier(0.16, 0.81, 0.32, 1) both;
        animation: iziM-revealIn 1s cubic-bezier(0.16, 0.81, 0.32, 1) both;
      }
      .iziModal-header-title, .iziModal-header-subtitle {
        -webkit-animation: iziM-slideIn 1s cubic-bezier(0.16, 0.81, 0.32, 1) both;
        -moz-animation: iziM-slideIn 1s cubic-bezier(0.16, 0.81, 0.32, 1) both;
        animation: iziM-slideIn 1s cubic-bezier(0.16, 0.81, 0.32, 1) both;
      }
      .iziModal-button {
        -webkit-animation: iziM-revealIn 1.2s cubic-bezier(0.7, 0, 0.3, 1);
        -moz-animation: iziM-revealIn 1.2s cubic-bezier(0.7, 0, 0.3, 1);
        animation: iziM-revealIn 1.2s cubic-bezier(0.7, 0, 0.3, 1);
      }
    }
    .iziModal-iframe, .iziModal-wrap {
      -webkit-animation: iziM-fadeIn 1.3s;
      -moz-animation: iziM-fadeIn 1.3s;
      animation: iziM-fadeIn 1.3s;
    }
    .iziModal-header {
      -webkit-animation-delay: 0.0s;
      -moz-animation: 0.0s;
      animation-delay: 0.0s;
      .iziModal-header-icon, .iziModal-header-title {
        -webkit-animation-delay: 0.4s;
        -moz-animation: 0.4s;
        animation-delay: 0.4s;
      }
      .iziModal-header-subtitle {
        -webkit-animation-delay: 0.5s;
        -moz-animation: 0.5s;
        animation-delay: 0.5s;
      }
    }
  }
  &.transitionOut .iziModal-header {
    transition: none !important;
    * {
      transition: none !important;
    }
  }
  &.fadeOut {
    -webkit-animation: iziM-fadeOut 0.5s;
    -moz-animation: iziM-fadeOut 0.5s;
    animation: iziM-fadeOut 0.5s;
    animation-fill-mode: forwards;
  }
}

